.products {
  padding-top: 121px;
  padding-bottom: 98px;
}

.title_box {
  width: 200px;
  margin: auto;
  @media screen and (min-width: 768px) {
    width: 283px;
  }
  @media screen and (min-width: 1280px) {
    width: 350px;
  }
}

.subtitle {
  margin: 0;
  padding-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
}

.title {
  margin: 0;
  padding-bottom: 31px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $button-and-heading-color;
}

.products_list {
  @media screen and (max-width: 767px) {
    display: block;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 700px;
    display: flex;
    justify-content: center;
  }

  @media screen and (min-width: 1280px) {
    width: 1054px;
    display: flex;
    justify-content: center;
  }
}

.products_list__item {
  position: relative;
  display: block;
  width: 280px;
  height: 598px;
  margin-bottom: 0;
  text-align: center;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    transform: translateY(-10px);
  }
  @media screen and (max-width: 767px) {
    margin-top: 0;
    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: calc((100% - 20px) / 3);
    height: 589px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @media screen and (min-width: 1280px) {
    width: calc((100% - 24px) / 3);
    height: 735px;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
}

.products_image {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  z-index: 2;
  display: block;
  max-width: 100%;
  margin: 0;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 249px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 220px;
  }

  @media screen and (min-width: 1280px) {
    width: 335px;
  }
}

.products_content {
  position: absolute;
  bottom: 0%;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  border-radius: 24px;

  @media screen and (max-width: 767px) {
    padding-top: 165px;
    padding-bottom: 40px;
    height: 485px;
    width: 280px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-top: 156px;
    padding-bottom: 40px;
    height: 485px;
    width: 220px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 170px;
    padding-bottom: 50px;
    max-height: 525px;
    width: 330px;
  }
}

.products_background__icecream {
  background-color: $hero-back-color;
}

.products_background__icecoffee {
  background-color: $heading-back-color-contacts-one;
}

.products_background__milkshakes {
  background-color: $heading-back-color-contacts-two;
}

.title_products {
  font-family: Titan One;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $white-color;
  position: relative;
  margin-top: 0px;

  &::after {
    content: '';
    border: 1px solid tomato;
    position: absolute;
    width: 26px;
    height: 6px;
    left: 50%;
    transform: translate(-50%);
    bottom: 0%;
    display: block;
    text-align: center;
    // background-image: url('');
  }
  @media screen and (max-width: 767px) {
    margin-bottom: 30px;
    padding-bottom: 30px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    margin-bottom: 30px;
    padding-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 36px;
    margin-bottom: 32px;
  }
}

.products_specification {
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
  font-family: DM Sans;
  color: $white-color;
  font-weight: 700;
  letter-spacing: 0.04em;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 200px;
    height: 124px;
    margin-top: 0;
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 152%;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 198px;
    height: 124px;
    margin-bottom: 40px;
    font-size: 14px;
    line-height: 176%;
  }

  @media screen and (min-width: 1280px) {
    width: 241px;
    height: 106px;
    margin-bottom: 62px;
    font-size: 16px;
    font-weight: 700;
    line-height: 176%;
  }
}

.products_button {
  display: block;
  margin-right: auto;
  margin-left: auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $white-color;
  border-style: none;
  padding: 0;
  cursor: pointer;
  &::after {
    content: '';
    // background-image: url('/src/images/arrow-right.svg');
    background-position: 50% 50%;
    background-repeat: no-repeat;
    display: inline-block;
    width: 40px;
    height: 40px;
  }
}
